<template>
  <div class="home">
    <!-- 顶部工作条 -->
    <div class="top">
      <Toolbar />
    </div>
    <div class="main">
      <!-- 左侧拖拽组件库 -->
      <div class="left">
        <ComponentList />
      </div>
      <!-- 中间画布 -->
      <div class="center">
        <div
          class="content"
          @dragover="handleDragOver"
          @drop="handleDrop"
          @mousedown="handleMouseDown"
          @mouseup="deselectCurComponent"
        >
          <Editor />
        </div>
      </div>
      <!-- 右侧属性列表 -->
      <div class="right">
        <el-tabs v-model="activeName">
          <!-- 属性 -->
          <el-tab-pane label="属性" name="attr">
            <AttrList v-if="curComponent" />
            <p v-else class="placeholder">请选择组件</p>
          </el-tab-pane>
          <!-- 动画 -->
          <el-tab-pane label="动画" name="animation">
            <AnimationList v-if="curComponent" />
            <p v-else class="placeholder">请选择组件</p>
          </el-tab-pane>
          <!-- 事件 -->
          <el-tab-pane label="事件" name="events">
            <EventList v-if="curComponent" />
            <p v-else class="placeholder">请选择组件</p>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import Toolbar from "../components/Toolbar";
import ComponentList from "../components/ComponentList";
import Editor from "../components/Editor/index";
import AttrList from "../components/AttrList";
import AnimationList from "../components/AnimationList";
import EventList from "../components/EventList";
import { deepCopy, getUuid } from "../utils/utils";
import componentData from "../func_components/listdata";

export default {
  name: "Home",
  components: {
    Toolbar,
    ComponentList,
    Editor,
    AttrList,
    AnimationList,
    EventList
  },
  data() {
    return {
      activeName: "attr"
    };
  },
  methods: {
    // 组件拖拽到画布途中事件
    handleDragOver(e) {
      e.preventDefault();
      e.dataTransfer.dropEffect = "copy";
    },
    // 组件拖拽到画布后结束事件
    handleDrop(e) {
      e.preventDefault();
      e.stopPropagation();
      const component = deepCopy(
        componentData[e.dataTransfer.getData("index")]
      );
      component.style.top = e.offsetY;
      component.style.left = e.offsetX;
      component.id = getUuid();
      this.$store.commit("addComponent", { component });
      this.$store.commit("recordSnapshot");
    },
    // 画布鼠标点击--组件点击失效
    handleMouseDown() {
      this.$store.commit("setClickComponentStatus", false);
    },
    // 画布离开鼠标--组件选取失效
    deselectCurComponent() {
      if (!this.isClickComponent) {
        this.$store.commit("hideContextMenu");
        this.$store.commit("setCurComponent", { component: null, index: null });
      }
    }
  },
  computed: {
    isClickComponent() {
      return this.$store.state.isClickComponent;
    },
    curComponent() {
      return this.$store.state.curComponent;
    }
  }
};
</script>

