<template>
  <div
    class="shape"
    :class="{ active }"
    @mousedown="handleMouseDownOnShape"
    @click="selectCurComponent"
  >
    <span class="iconfont icon-xiangyouxuanzhuan" v-show="isActive()"></span>
    <span class="iconfont icon-suo" v-show="element.isLock"></span>
    <div
      class="shape-point"
      v-for="item in isActive() ? pointList : []"
      @mousedown="handleMouseDownOnPoint(item, $event)"
      :key="item"
      :style="getPointStyle(item)"
    ></div>
    <slot></slot>
  </div>
</template>

<script>
import { mod360 } from "../../utils/translate";
import calculateComponentPositonAndSize from "../../utils/calculateComponentPositonAndSize";

export default {
  props: {
    active: {
      type: Boolean,
      default: false
    },
    element: {
      require: true,
      type: Object
    },
    defaultStyle: {
      require: true,
      type: Object
    },
    index: {
      require: true,
      type: [Number, String]
    }
  },
  data() {
    return {
      pointList: ["lt", "t", "rt", "r", "rb", "b", "lb", "l"], // 八个方向
      cursors: {}
    };
  },
  computed: {
    editor() {
      return this.$store.state.editor;
    }
  },
  methods: {
    isActive() {
      return this.active && !this.element.isLock;
    },
    getPointStyle(point) {
      const { width, height } = this.defaultStyle;
      const hasT = /t/.test(point);
      const hasB = /b/.test(point);
      const hasL = /l/.test(point);
      const hasR = /r/.test(point);
      let newLeft = 0;
      let newTop = 0;
      // 四个角的点
      if (point.length === 2) {
        newLeft = hasL ? 0 : width;
        newTop = hasT ? 0 : height;
      } else {
        // 上下两点的点，宽度居中
        if (hasT || hasB) {
          newLeft = width / 2;
          newTop = hasT ? 0 : height;
        }
        // 左右两边的点，高度居中
        if (hasL || hasR) {
          newLeft = hasL ? 0 : width;
          newTop = Math.floor(height / 2);
        }
      }
      const style = {
        marginLeft: hasR ? "-4px" : "-4px",
        marginTop: "-4px",
        left: `${newLeft}px`,
        top: `${newTop}px`,
        cursor: this.cursors[point]
      };
      return style;
    },
    handleMouseDownOnPoint(point, e) {
      this.$store.commit("setClickComponentStatus", true);
      e.stopPropagation();
      e.preventDefault();
      const style = { ...this.defaultStyle };
      // 组件宽高比
      const proportion = style.width / style.height;
      // // 组件中心点
      const center = {
        x: style.left + style.width / 2,
        y: style.top + style.height / 2
      };
      // // 获取画布位移信息
      const editorRectInfo = this.editor.getBoundingClientRect();
      // // 当前点击坐标
      const curPoint = {
        x: e.clientX - editorRectInfo.left,
        y: e.clientY - editorRectInfo.top
      };
      // 获取对称点的坐标
      const symmetricPoint = {
        x: center.x - (curPoint.x - center.x),
        y: center.y - (curPoint.y - center.y)
      };
      // // 是否需要保存快照
      let needSave = false;
      let isFirst = true;

      const needLockProportion = this.isNeedLockProportion();
      const move = moveEvent => {
        // 第一次点击时也会触发 move，所以会有“刚点击组件但未移动，组件的大小却改变了”的情况发生
        // 因此第一次点击时不触发 move 事件
        if (isFirst) {
          isFirst = false;
          return;
        }
        needSave = true;
        const curPositon = {
          x: moveEvent.clientX - editorRectInfo.left,
          y: moveEvent.clientY - editorRectInfo.top
        };
        calculateComponentPositonAndSize(
          point,
          style,
          curPositon,
          proportion,
          needLockProportion,
          {
            center,
            curPoint,
            symmetricPoint
          }
        );
        this.$store.commit("setShapeStyle", style);
      };

      const up = () => {
        document.removeEventListener("mousemove", move);
        document.removeEventListener("mouseup", up);
        needSave && this.$store.commit("recordSnapshot");
      };

      document.addEventListener("mousemove", move);
      document.addEventListener("mouseup", up);
    },
    // 组件在画布中移动事件
    handleMouseDownOnShape(e) {
      this.$store.commit("setClickComponentStatus", true);
      if (
        this.element.component !== "v-text" &&
        this.element.component !== "rect-shape"
      ) {
        e.preventDefault();
      }
      e.stopPropagation();
      this.$store.commit("setCurComponent", {
        component: this.element,
        index: this.index
      });
      if (this.element.isLock) return;
      // this.cursors = this.getCursor(); // 根据旋转角度获取光标位置
      const pos = { ...this.defaultStyle };
      const startY = e.clientY;
      const startX = e.clientX;
      // 如果直接修改属性，值的类型会变为字符串，所以要转为数值型
      const startTop = Number.parseInt(pos.top);
      const startLeft = Number.parseInt(pos.left);
      // 如果元素没有移动，则不保存快照
      let hasMove = false;
      // 组件拖动
      const move = moveEvent => {
        hasMove = true;
        const curX = moveEvent.clientX;
        const curY = moveEvent.clientY;
        pos.top = curY - startY + startTop;
        pos.left = curX - startX + startLeft;
        // 修改当前组件样式
        this.$store.commit("setShapeStyle", pos);
        // 等更新完当前组件的样式并绘制到屏幕后再判断是否需要吸附
        // 如果不使用 $nextTick，吸附后将无法移动
        this.$nextTick(() => {
          // 触发元素移动事件，用于显示标线、吸附功能
          // 后面两个参数代表鼠标移动方向
          // curY - startY > 0 true 表示向下移动 false 表示向上移动
          // curX - startX > 0 true 表示向右移动 false 表示向左移动
          // eventBus.$emit("move", curY - startY > 0, curX - startX > 0);
        });
      };
      // 组件停止拖动
      const up = () => {
        // 如果元素没有移动，则不保存快照
        if (hasMove) {
          this.$store.commit("recordSnapshot");
        }
        // 触发元素停止移动事件，用于隐藏标线
        // eventBus.$emit("unmove");
        document.removeEventListener("mousemove", move);
        document.removeEventListener("mouseup", up);
      };
      //
      document.addEventListener("mousemove", move);
      document.addEventListener("mouseup", up);
    },
    selectCurComponent(e) {
      e.stopPropagation();
      e.preventDefault();
      this.$store.commit("hideContextMenu");
    },
    //
    isNeedLockProportion() {
      if (this.element.component != "Group") return false;
      const ratates = [0, 90, 180, 360];
      for (const component of this.element.propValue) {
        if (!ratates.includes(mod360(parseInt(component.style.rotate)))) {
          return true;
        }
      }
      return false;
    }
  }
};
</script>

<style lang="less" scoped>
.shape {
  position: absolute;
  &:hover {
    cursor: move;
  }
}
.active {
  outline: 1px solid #70c0ff;
  user-select: none;
}
.shape-point {
  position: absolute;
  background: #fff;
  border: 1px solid #59c7f9;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  z-index: 1;
}
.icon-xiangyouxuanzhuan {
  position: absolute;
  top: -34px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 16px;
  font-weight: 600;
  cursor: grab;
  color: #59c7f9;
  font-size: 20px;
  font-weight: 600;
  &:active {
    cursor: grabbing;
  }
}
.icon-suo {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
