<template>
  <div
    class="editor"
    id="editor"
    :style="{
      width: canvasStyleData.width + 'px',
      height: canvasStyleData.height + 'px',
      transform: 'scale(' + parseInt(canvasStyleData.scale) / 100 + ')'
    }"
    @mousedown="handleMouseDown"
    @contextmenu="handleContextMenu"
  >
    <!-- 网格线 -->
    <!-- <Grid /> -->
    <!--页面组件列表展示-->
    <Shape
      v-for="(item, index) in componentData"
      :defaultStyle="item.style"
      :style="getShapeStyle(item.style)"
      :key="item.id"
      :element="item"
      :index="index"
      :active="item === curComponent"
      :class="{ lock: item.isLock }"
    >
      <component
        class="component"
        :is="item.component"
        :style="getComponentStyle(item.style)"
        :propValue="item.propValue"
        :element="item"
        :id="`component${item.id}`"
      />
    </Shape>
    <!-- 右击菜单 -->
    <ContextMenu />
  </div>
</template>

<script>
import Grid from "./Grid";
import Shape from "./Shape";
import ContextMenu from "./ContextMenu";
import { getStyle } from "../../utils/utils";

export default {
  components: {
    Grid,
    Shape,
    ContextMenu
  },
  mounted() {
    // 获取编辑器元素
    this.$store.commit("getEditor");
  },
  computed: {
    canvasStyleData() {
      return this.$store.state.canvasStyleData;
    },
    componentData() {
      return this.$store.state.componentData;
    },
    curComponent() {
      return this.$store.state.curComponent;
    }
  },
  methods: {
    // 显示右键菜单
    handleContextMenu(e) {
      e.stopPropagation();
      e.preventDefault();
      // 计算菜单相对于编辑器的位移
      let target = e.target;
      let top = e.offsetY;
      let left = e.offsetX;
      while (target instanceof SVGElement) {
        target = target.parentNode;
      }
      while (!target.className.includes("editor")) {
        left += target.offsetLeft;
        top += target.offsetTop;
        target = target.parentNode;
      }
      this.$store.commit("showContextMenu", { top, left });
    },
    handleMouseDown(e) {
      this.$store.commit("setClickComponentStatus", false);
      this.$store.commit("hideContextMenu");
      this.$store.commit("setCurComponent", { component: null, index: null });
      console.log("editor--mousedown");
      return;
      // 如果没有选中组件 在画布上点击时需要调用 e.preventDefault() 防止触发 drop 事件
      if (
        !this.curComponent ||
        (this.curComponent.component != "v-text" &&
          this.curComponent.component != "rect-shape")
      ) {
        e.preventDefault();
      }
      this.hideArea();
      // 获取编辑器的位移信息，每次点击时都需要获取一次。主要是为了方便开发时调试用。
      const rectInfo = this.editor.getBoundingClientRect();
      this.editorX = rectInfo.x;
      this.editorY = rectInfo.y;

      const startX = e.clientX;
      const startY = e.clientY;
      this.start.x = startX - this.editorX;
      this.start.y = startY - this.editorY;
      // 展示选中区域
      this.isShowArea = true;

      const move = moveEvent => {
        this.width = Math.abs(moveEvent.clientX - startX);
        this.height = Math.abs(moveEvent.clientY - startY);
        if (moveEvent.clientX < startX) {
          this.start.x = moveEvent.clientX - this.editorX;
        }

        if (moveEvent.clientY < startY) {
          this.start.y = moveEvent.clientY - this.editorY;
        }
      };

      const up = e => {
        document.removeEventListener("mousemove", move);
        document.removeEventListener("mouseup", up);

        if (e.clientX == startX && e.clientY == startY) {
          this.hideArea();
          return;
        }

        this.createGroup();
      };

      document.addEventListener("mousemove", move);
      document.addEventListener("mouseup", up);
    },
    getShapeStyle(style) {
      const result = {};
      ["width", "height", "top", "left", "rotate"].forEach(attr => {
        if (attr != "rotate") {
          result[attr] = style[attr] + "px";
        } else {
          result.transform = "rotate(" + style[attr] + "deg)";
        }
      });
      return result;
    },
    getComponentStyle(style) {
      return getStyle(style, ["top", "left", "width", "height", "rotate"]);
    }
  }
};
</script>

<style lang="less" scoped>
.editor {
  position: relative;
  background: #fff;
  margin: auto;
  .lock {
    opacity: 0.5;
  }
}
.edit {
  .component {
    outline: none;
    width: 100%;
    height: 100%;
  }
}
</style>
