<template>
	<div class="toolbar">
		<!-- 预览 -->
		<Preview :show="isShowPreview" @change="closePreview" />
		<!-- 功能按钮 -->
		<label for="input" class="insert">插入图片</label>
		<input type="file" id="input" hidden />
		<span class="gap">|</span>
		<el-button @click="undo" :disabled="!componentData.length">撤消</el-button>
		<el-button @click="redo" :disabled="!componentData.length">重做</el-button>
		<el-button @click="preview" :disabled="!componentData.length"
			>预览</el-button
		>
		<el-button @click="save" :disabled="!componentData.length">保存</el-button>
		<el-button @click="clearCanvas" :disabled="!componentData.length"
			>清空画布</el-button
		>
		<span class="gap">|</span>
		<el-button :disabled="!componentData.length">组合</el-button>
		<el-button :disabled="!componentData.length">拆分</el-button>
		<el-button @click="lock" :disabled="!curComponent || curComponent.isLock"
			>锁定</el-button
		>
		<el-button @click="unlock" :disabled="!curComponent || !curComponent.isLock"
			>解锁</el-button
		>
		<div class="canvas-config">
			<span>画布大小</span>
			<input v-model="canvasStyleData.width" />
			<span>*</span>
			<input v-model="canvasStyleData.height" />
		</div>
		<div class="canvas-config">
			<span>画布比例</span>
			<input v-model="canvasStyleData.scale" /> %
		</div>
	</div>
</template>

<script>
import Preview from "./Preview";

export default {
	components: {
		Preview
	},
	methods: {
		undo() {
			this.$store.commit("undo");
		},
		redo() {
			this.$store.commit("redo");
		},
		preview() {
			this.$store.commit("setIsShowPreview", true);
		},
		closePreview() {
			this.$store.commit("setIsShowPreview", false);
		},
		lock() {
			this.$store.commit("lock");
		},

		unlock() {
			this.$store.commit("unlock");
		},
		save() {
			console.log(this.componentData);
		},
		clearCanvas() {
			this.$store.commit("setComponentData", []);
			this.$store.commit("recordSnapshot");
		}
	},
	computed: {
		canvasStyleData() {
			return this.$store.state.canvasStyleData;
		},
		componentData() {
			return this.$store.state.componentData;
		},
		curComponent() {
			return this.$store.state.curComponent;
		},
		isShowPreview() {
			return this.$store.state.isShowPreview;
		}
	}
};
</script>

